import * as path from 'path';
import { Config } from 'karma';
import { resolvePackage } from '@microsoft/sp-build-core-tasks/lib/utilities/Resolve';
import { getConfig } from '@microsoft/gulp-core-build';

import { IExtendedKarmaConfig } from './karmaTypes';
import * as build from './../index';

const debugMode: boolean = process.argv.indexOf('--debug') !== -1;
const shipMode: boolean = process.argv.indexOf('--ship') !== -1 ||
                          process.argv.indexOf('--production') !== -1;

const configResources: any = build.karma.resources as any; // tslint:diable-line:no-any
const bindPolyfillPath: string = configResources.bindPolyfillPath;

const spBuildWebRoot: string = path.join(__dirname, '..', '..');
const spPolyfillsRoot: string = resolvePackage('@microsoft/sp-polyfills', spBuildWebRoot);
const spPolyfillsPath: string = path.join(spPolyfillsRoot, 'lib', 'FrameworkPolyfills.js');

import { Configuration } from 'webpack';
const webpackConfig: Configuration = build.configureWebpack.getKarmaConfig(
  (debugMode ? [] : [
    {
      test: /\.js$/,
      // @todo VSO:247624 - clean this up when localized resources are properly loaded
      exclude: /(test|node_modules|bower_components|globalize-locales|resx-strings|localized_timezone_cities)/,
      loader: configResources.istanbulInstrumenterLoaderPath
    }
  ]),
  [{
    'jsdom': 'window',
    'cheerio': 'window',
    'react/addons': true,
    'react/lib/ExecutionEnvironment': true,
    'react/lib/ReactContext': true
  }]
);
webpackConfig.context = getConfig().rootPath;

const testsJsFilePath: string = path.join(getConfig().tempFolder, 'tests.js');

export = (config: Config) => {
  const karmaConfig: IExtendedKarmaConfig = {

    // base path that will be used to resolve all patterns (eg. files, exclude)
    basePath: getConfig().rootPath,

    // frameworks to use
    // available frameworks: https://npmjs.org/browse/keyword/karma-adapter
    frameworks: ['mocha', 'sinon-chai'],

    // list of files / patterns to load in the browser
    files: [bindPolyfillPath].concat([
      spPolyfillsPath,
      testsJsFilePath]
    ),

    // list of files to exclude
    exclude: [],

    // webpack config for bundling tests.
    webpack: webpackConfig,

    webpackMiddleware: {
      noInfo: true,
      quiet: !debugMode
    },

    // preprocess matching files before serving them to the browser
    // available preprocessors: https://npmjs.org/browse/keyword/karma-preprocessor
    preprocessors: {
      [testsJsFilePath]: ['webpack'],
      [path.join(getConfig().libFolder, '**/*.js')]: ['webpack'],
      [spPolyfillsPath]: ['webpack']
    },

    plugins: configResources.plugins.concat([
      require('./karma-test-result-reporter')
    ]),

    // test results reporter to use
    // possible values: 'dots', 'progress'
    // available reporters: https://npmjs.org/browse/keyword/karma-reporter
    reporters: ['test-result', 'mocha-clean', 'coverage'],

    testResultReporter: {
      outputFile: path.join(getConfig().tempFolder, 'testResult/result.json')
    },

    coverageReporter: {
      dir: path.join(getConfig().tempFolder, 'coverage'),
      reporters: [
        {
          type: 'html',
          subdir: 'js'
        },
        {
          type: 'json',
          subdir: './',
          file: 'js-coverage.json'
        }
      ]
    },

    // web server port
    port: 9876,

    // enable / disable colors in the output (reporters and logs)
    colors: true,

    // level of logging
    // possible values: config.LOG_DISABLE || config.LOG_ERROR || config.LOG_WARN || config.LOG_INFO || config.LOG_DEBUG
    logLevel: (shipMode ? config.LOG_INFO : config.LOG_DEBUG),

    // enable / disable watching file and executing tests whenever any file changes
    autoWatch: false,

    // start these browsers
    // available browser launchers: https://npmjs.org/browse/keyword/karma-launcher
    browsers: ['PhantomJS'],

    // Continuous Integration mode
    // if true, Karma captures browsers, runs the tests and exits
    singleRun: true,

    // Increase timeouts to reduce random CI failure
    // See https://github.com/karma-runner/karma/issues/598
    captureTimeout: 10000,             // default 2000
    browserDisconnectTimeout : 5000,   // default 2000
    browserDisconnectTolerance : 5,    // default 0
    browserNoActivityTimeout : 20000   // default 10000
  };

  config.set(karmaConfig);
};
